				"html": "<p>Based on <a href=\"http://ricostacruz.com/cheatsheets/react-router.html\">http://ricostacruz.com/cheatsheets/react-router.html</a> but updated for v2.0.0 of React Router.</p>\n<h2>Basic</h2>\n<pre><code>import { render } from 'react-dom'\nimport { Router, Route, browserHistory } from 'react-router'\n\n// Import RootView and NoMatch\n\nrender((\n  &#x3C;Router history={browserHistory}>\n    &#x3C;Route path=\"/\" component={RootView} />\n    &#x3C;Route path=\"*\" component={PageNotFound} />\n  &#x3C;/Router>\n), document.getElementById('app'))\n</code></pre>\n<h2>Nesting</h2>\n<pre><code>import React from 'react'\nimport { render } from 'react-dom'\nimport { Router, Route, browserHistory } from 'react-router'\n\n// Import About, Inbox and Messages\n\nclass Chrome extends React {\n  render () {\n    return (\n      &#x3C;div>\n        &#x3C;h1>App&#x3C;/h1>\n        &#x3C;a href=\"/about\">About&#x3C;/a>\n        &#x3C;a href=\"/inbox\">Inbox&#x3C;/a>\n        &#x3C;a href=\"/messages\">Messages&#x3C;/a>\n        { this.props.children }\n      &#x3C;/div>\n    )\n  }\n}\n\nrender((\n  &#x3C;Router history={browserHistory}>\n    &#x3C;Route path=\"/\" component={Chrome}>\n      &#x3C;Route path=\"about\" component={About} />\n      &#x3C;Route path=\"inbox\" component={Inbox} />\n      &#x3C;Route path=\"messages\" component={Messages} />\n    &#x3C;/Route>\n  &#x3C;/Router>\n), document.getElementById('app'))\n</code></pre>\n<h2>URL params</h2>\n<pre><code>class Message extends React {\n  componentDidMount() {\n    // from the path `/inbox/messages/:id`\n    const id = this.props.params.id\n    ...\n</code></pre>\n<h2>Link</h2>\n<pre><code>import { Link } from 'react-router'\n\n/* Nav Component */\n  ...\n  render() {\n    ...\n    const userId = 10;\n\n    return (\n      // Adding params is as simple as appending them to the route\n      &#x3C;Link to={`user/${userId}`}>User 10&#x3C;/Link>\n\n      // Classes can be added to the link element if the current route is the one they link to\n      &#x3C;Link to=\"login\"\n        activeClassName=\"-active\">Login&#x3C;/Link>\n    )\n  }\n</code></pre>\n<h2>Other config</h2>\n<pre><code>&#x3C;Route path=\"/\">\n  &#x3C;IndexRoute component={Home} />\n  &#x3C;Route path=\"*\" handler={NotFound} />\n\n  // arbitrary/url/login -> /arbitrary/url/sessions/new\n  &#x3C;Redirect from=\"login\" to=\"sessions/new\" />\n  // arbitrary/url/about/1 -> /arbitrary/url/profile/1\n  &#x3C;Redirect from=\"about/:id\" to=\"profile/:id\" />\n\n  &#x3C;Route name=\"about-user\" ... />\n  ...\n</code></pre>",
